.subtext {
  font-size: .9em;
}

.primary-text {
  color: #d2383f !important;
}

.secondary-text {
  color: #fff;
}

.dark-text {
  color: #3d3e44 !important;
}

.uppercase-header-sm {
  font-size: 0.8em;
  font-weight: 500;
  letter-spacing: 1px;
  text-transform: uppercase;
}

.tiny-text-uppercase {
  font-size: 0.5em;
  letter-spacing: 1px;
  text-transform: uppercase;
}

p {
  color: #3d3e44;
  margin: 0 0 1.5rem;
  font-size: .8em;
  line-height: 1.7;
}

body {
  font-size: .9em;
  line-height: 1.7;
}

.paragraph-primary {
  font-size: 1.2em;
}

p a {
  border-color: rgba(61, 62, 68, 0.3);
  border-bottom-style: solid;
  border-bottom-width: 1px;
  text-decoration: none;
  color: #3d3e44;
}

a {
  transition: color 150ms ease-in-out, border-color 150ms ease-in-out;
  background-color: transparent;
}

.navbar-brand:hover {
  color: white !important;
  border-bottom-style: solid;
  border-bottom-width: 1px;
  text-decoration: none;
  border-bottom-color: #fff;
}

a:hover {
  color: #d2383f !important;
  border-bottom-style: solid;
  border-bottom-width: 1px;
  text-decoration: none;
  border-bottom-color: #d2383f;
}

.skill-list li a:hover {
  color: #d2383f !important;
  border-bottom-style: solid;
  border-bottom-width: 1px;
  text-decoration: none;
  border-bottom-color: #d2383f;
}

.nav-item a:hover {
  color: #fff !important;
  border-bottom-style: none;
  text-decoration: none;
}

.yellow-text {
  color: #ff931e;
}

.blue-text {
  color: #177be3;
}

.green-text {
  color: #18b290;
}

footer a {
  color: #b5b5c0;
}

.navigation-group-title {
  font-size: 1.2em;
  margin: 0 0 0.75rem;
}

.navigation-group li {
  margin-bottom: 0.375rem;
  line-height: 1.7;
}

h3 {
  font-size: 1.5em;
  line-height: 1.25;
  display: block;
}

.grey-text {
  color: #5a5b60;
}

.button-primary {
  color: #d2383f;
  background-color: #fff;
  border: 0;
  font-size: 1em;
  font-weight: 500;
  line-height: 1.9;
  min-height: 1.5rem;
  padding: 0.75rem 1.5rem;
  text-decoration: none;
  border-radius: 1px;
}

.nav-item .button-primary {
  color: #d2383f;
  background-color: #fff;
  border: 0;
  font-size: 1em;
  font-weight: 500;
  line-height: normal;
  min-height: inherit;
  padding: 0.75rem 1.5rem;
  border-radius: 1px;
}

.button-secondary {
  color: #fff;
  background-color: #d2383f;
  border: 0;
  font-size: 1em;
  font-weight: 500;
  line-height: 1.9;
  min-height: 1.5rem;
  padding: 0.75rem 1.5rem;
  text-decoration: none;
  border-radius: 1px;
}

.button-secondary:hover {
  color: #fff !important;
  border-bottom-style: none;
  text-decoration: none;
}

.img-container {
  position: relative;
  background: #000;
  overflow: hidden;
}

.img-container:after {
  content: ' ';
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  opacity: 0.15;
  background-image: url("/assets/img/codemena-offices.jpg");
  background-repeat: no-repeat;
  background-position: center;
  -ms-background-size: cover;
  -o-background-size: cover;
  -moz-background-size: cover;
  -webkit-background-size: cover;
  background-size: cover;
}

.nav-link {
  color: #fff !important;
}

.navbar-brand {
  font-weight: 600 !important;
  color: #fff !important;
}

.navbar-toggler-icon {
  color: #fff !important;
}

.button-primary .nav-link {
  color: #d2383f;
}

.blockquote-primary {
  color: #fff;
  font-weight: 600;
  font-size: 1.5em;
}

.icon-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  margin-top: 3rem;
  box-sizing: border-box;
}

.icon-list::after {
  clear: both;
  content: "";
  display: block;
}

.icon-item {
  float: left;
  display: list-item;
  position: relative;
  padding-bottom: 1.5rem;
  padding-right: 3rem;
}

.icon-item h2 {
  display: block;
  margin: 0 0 0.75rem;
}

*, *::after, *::before {
  box-sizing: inherit;
}

.icon-item ul {
  margin-bottom: 0.75rem;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: block;
}

.icon-item ul a {
  border-color: rgba(181, 181, 192, 0.5);
  color: #5a5b60;
  border-bottom-style: solid;
  border-bottom-width: 1px;
  text-decoration: none;
  font-size: 0.9rem;
}

.icon-item ul li {
  line-height: 1.9;
}

@media screen and (min-width: 580px) {
  .icon-item:nth-child(2n+1) {
    clear: left;
  }
}

.bulleted-list-primary {
  list-style: circle;
  padding: 1.125rem;
  list-style-type: circle;
}

.bulleted-list-primary li {
  font-size: .9em;
  color: #5a5b60;
}
